.table-wrapper {
  overflow-x: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

tr:hover {
  background-color: #ddd;
}

button {
  padding: 5px 10px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  color: white;
}

button.update {
  background-color: #4CAF50;
}

button.delete {
  background-color: #f44336;
}

.form-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.member-form {
  border: 1px solid #ddd;
  padding: 20px;
  /* max-width: 400px; */
}






.radio-row {
  display: flex;
  flex-direction: row;
}

.radio-column {
  display: flex;
  flex-direction: column;
}



/* RegisterForm.css */


  
.form {
  width: 120%;
 
  background-color: #c8d4cc;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
}

.form h2 {
  text-align: center;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

input[type="text"],
input[type="number"],
select,
input[type="tel"],
input[type="email"] {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button[type="submit"] {
  width: 100%;
  padding: 10px;
  background-color: #119146;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}

.submissions {
  margin-top: 30px;
  padding: 20px;
  overflow-x: auto; 
}

.submissions table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #ccc;
}

.submissions th,
.submissions td {
  padding: 10px;
  border: 1px solid #ccc;
  text-align: left;
}

.submissions th {
  background-color: #007bff;
  color: #fff;
}

.submissions td button {
  padding: 5px 10px;
  background-color: #0b4e2f;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 5px;
}

.submissions td button:hover {
  background-color: #4b8543;
}

/* Media Query for Small Screens */
@media screen and (max-width: 600px) {
  .form {
    padding: 10px;
  }

  .form h2 {
    font-size: 1.5em;
  }

  .form-group label {
    font-size: 0.9em;
  }

  input[type="text"],
  input[type="number"],
  select,
  input[type="tel"],
  input[type="email"] {
    font-size: 0.9em;
  }

  button[type="submit"] {
    font-size: 1em;
  }
}


.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
}

.form-group input[type="text"],
.form-group input[type="number"],
.form-group input[type="email"] {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

/* Add red border for required inputs */
.form-group input[type="text"][required],
.form-group input[type="number"][required],
.form-group input[type="email"][required] {
  border-color: red;
}
